html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.8rem;
  background-color: #18181B;
}

a {
  text-decoration: none;
  color: #aaaaaa;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
}

h1, h2, h3, h4 {
  font-family: "Jost", sans-serif;
  margin: 0;
  font-weight: 900;
  color: #FFFFFF;
  text-transform: uppercase;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #FFFFFF;
  line-height: 1.8;
}

.title {
  margin-bottom: 4rem;
}

[class$=__heading] {
  text-transform: uppercase;
  font-weight: 800;
  font-size: 2.2rem;
  margin: 0;
}

.header {
  width: min(95%, 120rem);
  margin: 0 auto;
  width: 100%;
  background: rgba(30, 30, 33, 0.3);
  backdrop-filter: blur(2.6px);
  -webkit-backdrop-filter: blur(2.6px);
}
.header .header__container {
  height: 6rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.header .header__logo .logo__image {
  width: 120px;
  transition: all 0.5s;
}
.header .header__logo .logo__image:hover {
  transform: scale(0.9);
}
.header .navbar__links {
  display: flex;
  column-gap: 2rem;
}
.header .navbar__links a {
  font-size: 1.6rem;
  opacity: 0.8;
  font-weight: 600;
  transition: all 0.5s;
}
.header .navbar__links a:hover {
  opacity: 1;
  color: #FFFFFF;
}
.header .header__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 1rem;
}
.header .header__btn a,
.header .header__btn buttom {
  background-color: #FFFFFF;
  padding: 0.8rem 1.5rem;
  border-radius: 1.5rem;
  font-size: 1.6rem;
  color: #18181B;
  transition: all 0.5s;
}
.header .header__btn a:hover,
.header .header__btn buttom:hover {
  opacity: 0.8;
  cursor: pointer;
}
.header .darkmode__btn {
  font-size: 1.6rem;
  padding: 1rem;
  border: none;
  border-radius: 1.5rem;
  transition: all 0.5s;
}
.header .darkmode__btn:hover {
  opacity: 0.8;
  cursor: pointer;
}

.active {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 1;
}
.active a {
  color: #FFFFFF;
  opacity: 1;
}

.main {
  width: min(95%, 120rem);
  margin: 0 auto;
}
.main .main__container {
  height: calc(100vh - 6rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main .main__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main .main__content .main__headings h1 {
  animation-name: toLeft;
  font-size: 7rem;
}
.main .main__content .main__headings h2 {
  animation-name: toRight;
}
.main .main__content .main__headings h1, .main .main__content .main__headings h2 {
  animation-duration: 5s;
  animation-timing-function: ease;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  letter-spacing: 0.3rem;
}
.main .main__text {
  margin: 2rem 0;
  width: 60%;
  text-align: center;
}
.main .main__text p {
  font-size: 1.6rem;
  font-weight: 200;
}
.main .main__buttons {
  display: flex;
  column-gap: 3rem;
}
.main .main__buttons .main__button {
  border-radius: 1.6rem;
  background-color: #1F1F23;
  -webkit-box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  -moz-box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  display: flex;
  column-gap: 1rem;
  padding: 1rem 1.4rem;
  font-size: 1.4rem;
}
.main .main__buttons .main__button span {
  transition: all 0.5s;
}
.main .main__buttons .main__button:hover span {
  color: #2563eb;
}

@keyframes toLeft {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-2rem);
  }
}
@keyframes toRight {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(2rem);
  }
}
.experience {
  width: min(95%, 120rem);
  margin: 0 auto;
}
.experience .experience__cards {
  display: flex;
  justify-content: space-around;
  align-items: center;
  column-gap: 3rem;
}
.experience .experience__cards .experience__card {
  border-radius: 1.6rem;
  background-color: #1F1F23;
  -webkit-box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  -moz-box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  padding: 4rem 6rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.experience .experience__cards .experience__card .card__head {
  border-bottom: 1px solid #18181B;
}
.experience .experience__cards .experience__card .card__head h4 {
  font-size: 2.6rem;
}
.experience .experience__cards .experience__card .card__head p {
  margin: 0.5rem 0 0.8rem 0;
  font-size: 1.3rem;
}
.experience .experience__cards .experience__card .card__content h5 {
  font-size: 2rem;
  margin: 2rem 0 1rem 0;
  color: #FFFFFF;
  font-weight: 400;
}
.experience .experience__cards .experience__card .card__content .experience__list {
  color: #FFFFFF;
  font-size: 1.4rem;
  line-height: 1.5;
  list-style: square;
  padding-left: 4rem;
}
.experience .experience__cards .experience__card .card__content .experience__tools p {
  padding-left: 4rem;
  margin: 0;
  font-size: 1.6rem;
}

.projects {
  width: min(95%, 120rem);
  margin: 0 auto;
  margin-top: 8rem;
}
.projects .projects__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
.projects .projects__cards .project__card {
  border-radius: 1.6rem;
  background-color: #1F1F23;
  -webkit-box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  -moz-box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  padding: 0 1.5rem;
}
.projects .projects__cards .project__card .project-card__head h5 {
  color: #FFFFFF;
  font-size: 2rem;
  margin: 1.5rem 0;
}
.projects .projects__cards .project__card .project-card__content {
  padding: 0 0.2rem;
}
.projects .projects__cards .project__card .project-card__content p {
  font-size: 1.6rem;
  text-align: justify;
  line-height: 1.5;
}

.about {
  width: min(95%, 120rem);
  margin: 0 auto;
  margin-top: 8rem;
}
.about h2 {
  text-align: center;
}
.about .about__content {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 4rem;
}
.about .about__content .about__description {
  width: 70%;
}
.about .about__content .about__description h3 {
  font-size: 3rem;
}
.about .about__content .about__description h3 span {
  display: block;
  font-size: 5rem;
}
.about .about__content .about__description p {
  font-size: 1.6rem;
  text-align: justify;
}
.about .about__content .about__image {
  width: 40rem;
  height: 40rem;
  background-image: url(../img/elias-tofaletti.png);
  background-size: cover;
  background-position: center;
  border-radius: 5rem;
  background-color: #2563eb;
  transform: rotate(-2deg);
  transition: all 0.5s;
}
.about .about__content .about__image:hover {
  transform: rotate(0);
}

.about__education {
  margin-top: 5rem;
}
.about__education .education__cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
.about__education .education__cards .education__card {
  border-radius: 1.6rem;
  background-color: #1F1F23;
  -webkit-box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  -moz-box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  padding: 4rem 3rem 1rem 3rem;
}
.about__education .education__cards .education__card .education-card__head h4 {
  font-weight: 600;
}
.about__education .education__cards .education__card .education-card__head p {
  font-size: 1.4rem;
  color: #aaaaaa;
}
.about__education .education__cards .education__card .education-card__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about__education .education__cards .education__card .education-card__info .education__location {
  font-size: 1.4rem;
  font-weight: 800;
}
.about__education .education__cards .education__card .education-card__info .education__date {
  font-size: 1.2rem;
  font-style: italic;
  color: #aaaaaa;
}

.about__certification {
  margin-top: 5rem;
}
.about__certification .certifications__cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
.about__certification .certifications__cards .certification__card {
  border-radius: 1.6rem;
  background-color: #1F1F23;
  -webkit-box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  -moz-box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  padding: 2rem;
}
.about__certification .certifications__cards .certification__card .certification-card__head h4 {
  font-weight: 600;
}
.about__certification .certifications__cards .certification__card .certification__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
}
.about__certification .certifications__cards .certification__card .certification__info .certification__location {
  font-size: 1.4rem;
  font-weight: 800;
  margin: 0;
}
.about__certification .certifications__cards .certification__card .certification__info .certification__link {
  display: flex;
  column-gap: 0.5rem;
  justify-content: space-between;
  align-items: center;
  font-size: 1.2rem;
  color: #aaaaaa;
}
.about__certification .certifications__cards .certification__card .certification__info .certification__link span {
  font-size: 1.2rem;
}
.about__certification .certifications__cards .certification__card .certification__info .certification__link:hover {
  text-decoration: underline;
}

.contact {
  width: min(95%, 120rem);
  margin: 0 auto;
}
.contact .contact__container {
  height: calc(100vh - 6rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.contact .contact__container .contact__title h2 {
  font-size: 7rem;
}
.contact .contact__container .contact__title p {
  font-size: 1.8rem;
}
.contact .contact__container .contact__email p {
  border: none;
  background-color: #1F1F23;
  font-size: 2.6rem;
  font-weight: 700;
  letter-spacing: 0.1rem;
  color: #FFFFFF;
  padding: 1.2rem;
  border-radius: 3rem;
}
.contact .contact__container .contact__email button {
  border: none;
  font-size: 1.8rem;
  padding: 1.2rem;
  border-radius: 1.8rem;
  margin: 0;
}
.contact .contact__container .contact__email button:hover {
  cursor: pointer;
}
.contact .contact__container .contact__email .copied {
  padding: 1.2rem;
  color: #3de72e;
  border: 1px solid #3de72e;
  background-color: #18181B;
}
.contact .contact__container .contact__buttons {
  margin-top: 5rem;
  display: flex;
  column-gap: 3rem;
}
.contact .contact__container .contact__buttons .contact__button {
  display: flex;
  column-gap: 1rem;
  padding: 1rem 1.4rem;
  font-size: 1.4rem;
  transition: all 0.5s;
}
.contact .contact__container .contact__buttons .contact__button span {
  transition: all 0.5s;
}
.contact .contact__container .contact__buttons .contact__button:hover, .contact .contact__container .contact__buttons .contact__button:hover span {
  color: #2563eb;
  border-radius: 1.6rem;
  background-color: #1F1F23;
  -webkit-box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  -moz-box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
  box-shadow: 0px 0px 24px -8px rgb(18, 18, 18);
}

.footer {
  width: min(95%, 120rem);
  margin: 0 auto;
  width: 100%;
  height: 6rem;
  border-top: 1px solid #1F1F23;
}
.footer .footer__content {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.footer .footer__content p {
  font-size: 1.4rem;
  font-weight: 400;
  color: #aaaaaa;
}
.footer .footer__content p a {
  font-weight: 600;
  transition: all 0.5s;
}
.footer .footer__content p a:hover {
  color: #2563eb;
}